.activity {
  font-weight: bold;
}

.activity__title {
  font-size: 28px;
  line-height: 32px;
  color: $light-black;
  padding-bottom: 24px;
}

.activity__wrapper {
  display: flex;
  flex-direction: column;
}

.activity__send-us-wrapper {
  background-color: $background-color-2;
  border-bottom-left-radius: 32px;
  border-bottom-right-radius: 32px;
  padding: 0 32px;
  padding-top: 64px;
  padding-bottom: 32px;
  margin-top: -32px;
}

.activity__send-us-wrapper,
.activity__opportunity-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 20px;
  line-height: 28px;
}

.activity__send-us {
  color: $light-black;
}

.activity__send-us br:nth-child(5) {
  display: none;
}

.activity__opportunity-wrapper {
  position: relative;
  flex-grow: 1;
  background-color: $light-black;
  border-radius: 32px;
  overflow: hidden;
  padding-left: 32px;
  z-index: 1;
}

.activity__opportunity-wrapper-bg {
  margin-left: auto;
}

.activity__opportunity {
  position: absolute;
  color: $basic-white;
  display: grid;
  grid-template-rows: max-content max-content;
  row-gap: 30px;
  bottom: 32px;
}

.activity__opportunity p {
  margin: 0;
}

@media (min-width: $tablet-width) {
  .activity__title {
    font-size: 48px;
    line-height: 56px;
  }

  .activity__opportunity {
    top: 50%;
    transform: translateY(-10%);
  }

  .activity__send-us {
    white-space: nowrap;
  }

  .activity__send-us-wrapper {
    padding-left: 40px;
  }

  .activity__send-us br {
    display: none;
  }

  .activity__send-us br:nth-child(2) {
    display: block;
  }

  .activity__send-us br:nth-child(5) {
    display: block;
  }

  .activity__wrapper {
    padding: 0 40px;
  }
}

@media (min-width: $desktop-width) {
  .activity__send-us br {
    display: block;
  }

  .activity__title {
    font-size: 80px;
    line-height: 100px;
  }

  .activity__wrapper {
    min-height: 640px;
    padding: 0 120px;
    flex-direction: row;
  }

  .activity__send-us-wrapper {
    padding: 0 40px;
    border-radius: 40px;
  }

  .activity__send-us-wrapper,
  .activity__opportunity-wrapper {
    font-size: 24px;
    line-height: 32px;
  }

  .activity__opportunity-wrapper {
    padding-left: 40px;
    border-radius: 40px;
    margin-right: 40px;
  }
}
